*{ 
	margin:0px; 
	padding:0px; 
	border-radius: 0px; 
	box-sizing: border-box; 
	font-size: 110%; 
} 

.calculator-title{ 
	font-size:30px; 
	background: #fff; 
	width: 400px; 
	padding: 10px 10px; 
	margin: 0 auto; 
	margin-top: 20px; 
	margin-bottom: 20px; 
	border-radius: 2px; 
	border: 2px solid black; 
	color: #000000; 
} 


.mainCalc{ 
	margin:0px; 
	padding:0px; 
	border-radius: 0px; 
	box-sizing: border-box; 
} 

input[type='button']{ 
	width: 100px; 
	background: #424b4300; 
	border: 1px solid #222; 
	padding: 10px 20px; 
	color: black; 
	text-align: center; 
	text-decoration: none; 
	display: inline-block; 
	font-size: 16px; 
} 

input[type='button']:active{ 
	background: rgba(37, 26, 26, 0); 
} 


#root{ 
	text-align:center; 
} 

.screen-row input{ 
width: 400px; 
	background: #ddd; 
	border: 0px; 
	color: #222; 
	padding: 10px; 
	text-align: right; 
}
